import { SingleModelResponse } from "./SingleModelResponse";
import { JobProcessedSqlViewModel } from "../ViewModel/JobProcessedSqlViewModel";

export class JobProcessedSqlResponse extends SingleModelResponse<
  JobProcessedSqlViewModel
> {
  Model: JobProcessedSqlViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      this.Model = new JobProcessedSqlViewModel();
      this.Model.ssis_job_no = obj.Model.ssis_job_no; //SSIS
      this.Model.exec_status_key = obj.Model.exec_status_key; //
      this.Model.sql_statement = (obj && obj.Model.sql_statement) || null; //sql
      this.Model.is_active = obj.Model.is_active; //
      this.Model.creator = obj.Model.creator; //
      this.Model.create_time = obj.Model.create_time; //
      this.Model.modifier = obj.Model.modifier; //
      this.Model.last_update_time = obj.Model.last_update_time; //
    }
  }
}



