import { SingleModelResponse } from "./SingleModelResponse";
import { JobLoopEventViewModel } from "../ViewModel/JobLoopEventViewModel";

export class JobLoopEventResponse extends SingleModelResponse<
  JobLoopEventViewModel
> {
  Model: JobLoopEventViewModel;
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Message;
    this.DidError = obj.DidError;
    this.ErrorMessage = obj.ErrorMessage;
    if (this.DidError === false) {
      if (obj.Model.loop_seq) {
        this.Model = new JobLoopEventViewModel();
        this.Model.loop_seq = obj.Model.loop_seq; //
        this.Model.ssis_job_no = obj.Model.ssis_job_no; //SSIS
        this.Model.job01 = (obj && obj.Model.job01) || null; //
        this.Model.job02 = (obj && obj.Model.job02) || null; //
        this.Model.job03 = (obj && obj.Model.job03) || null; //
        this.Model.event_type_key = obj.Model.event_type_key; //
        this.Model.src_path_key = obj.Model.src_path_key; //
        this.Model.file_keyword = (obj && obj.Model.file_keyword) || null; //
        this.Model.remote_conn_id = (obj && obj.Model.remote_conn_id) || null; //
        this.Model.period_min = obj.Model.period_min; //
        this.Model.active_start_date = obj.Model.active_start_date; //
        this.Model.active_start_time = obj.Model.active_start_time; //
        this.Model.next_start_time = obj.Model.next_start_time; //
        this.Model.sbp_conn_id = obj.Model.sbp_conn_id; //sbpclient
        this.Model.is_active = obj.Model.is_active; //
        this.Model.is_active_desc = obj.Model.is_active_desc; //
        this.Model.creator = obj.Model.creator; //
        this.Model.create_time = obj.Model.create_time; //
        this.Model.modifier = obj.Model.modifier; //
        this.Model.last_update_time = obj.Model.last_update_time; //
      }
    }
  }
}



