import { JobLoopEventViewModel } from "../ViewModel/JobLoopEventViewModel";
import { ListModelResponse } from "./ListModelResponse";

export class JobLoopEventListResponse extends ListModelResponse<
  JobLoopEventViewModel
> {
  Model: JobLoopEventViewModel[];
  constructor(obj?: any) {
    super(obj);
    this.Message = obj.Messag;
    this.DidError = obj.DidError;
    this.ErrorMessage = (obj && obj.ErrorMessage) || null;
    this.PageNumber = obj.PageNumber;
    this.PageSize = obj.PageSize;
    this.PageRows = obj.PageRows;
    this.TotalPages = obj.TotalPages;
    this.TotalRows = obj.TotalRows;
    if (this.DidError === false) {
      this.Model = [];
      obj.Model.forEach(data => {
        let item = new JobLoopEventViewModel();
        item.loop_seq = data.loop_seq; //
        item.ssis_job_no = data.ssis_job_no; //SSIS
        item.job01 = (data && data.job01) || null; //
        item.job02 = (data && data.job02) || null; //
        item.job03 = (data && data.job03) || null; //
        item.event_type_key = data.event_type_key; //
        item.event_type = data.event_type; //
        item.src_path_key = data.src_path_key; //
        item.src_path = data.src_path; //
        item.file_keyword = (data && data.file_keyword) || null; //
        item.remote_conn_id = (data && data.remote_conn_id) || null; //
        item.remote_conn_string = (data && data.remote_conn_string) || null; //
        item.period_min = data.period_min; //
        item.active_start_date = data.active_start_date; //
        item.active_start_time = data.active_start_time; //
        item.next_start_time = data.next_start_time; //
        item.sbp_conn_id = data.sbp_conn_id; //sbpclient
        item.sbp_conn_string = data.sbp_conn_string; //sbpclient
        item.is_active = data.is_active; //
        item.is_active_desc = data.is_active_desc; //
        item.creator = data.creator; //
        item.create_time = data.create_time; //
        item.modifier = data.modifier; //
        item.last_update_time = data.last_update_time; //
        item.event_type_desc=data.event_type_key+"-" +data.event_type;
        switch (data.is_active) {
          case false: {
            item.is_active_color = "danger-left";
            break;
          }

          default:
            item.is_active_color = "active-left";
        }
        this.Model.push(item);
      }) || null;
    }
  }
}



